import React, { useState, useEffect } from "react";
import {
  Container,
  Row,
  Col,
  Card,
  Form,
  Button,
  Alert,
  Spinner,
  Badge,
  Modal,
} from "react-bootstrap";
import { useAuth } from "../contexts/AuthContext";
import { useSubscription } from "../contexts/SubscriptionContext";
import { useNavigate } from "react-router-dom";

const Profile = () => {
  const { userProfile, updateProfile, currentUser, profileLoading } = useAuth();
  const { autoSyncActive } = useSubscription();
  const navigate = useNavigate();

  const [formData, setFormData] = useState({
    nombres: "",
    apellidos: "",
    telefono: "",
    pais: "",
  });
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);
  const [isEditing, setIsEditing] = useState(false);
  const [subscription, setSubscription] = useState(null);
  const [subscriptionLoading, setSubscriptionLoading] = useState(true);
  const [syncLoading, setSyncLoading] = useState(false);
  const [syncSuccess, setSyncSuccess] = useState("");
  const [plans, setPlans] = useState({});
  const [hasActiveSubscription, setHasActiveSubscription] = useState(false);
  const [isInGracePeriod, setIsInGracePeriod] = useState(false);
  const [showCancelModal, setShowCancelModal] = useState(false);
  const [actionLoading, setActionLoading] = useState(false);

  // Load user profile data
  useEffect(() => {
    if (userProfile) {
      setFormData({
        nombres: userProfile.nombres || "",
        apellidos: userProfile.apellidos || "",
        telefono: userProfile.telefono || "",
        pais: userProfile.pais || "",
      });
    }
  }, [userProfile]);

  // Load subscription data
  useEffect(() => {
    if (currentUser) {
      loadSubscription();
      loadPlans();
    }
  }, [currentUser]);

  const loadSubscription = async () => {
    try {
      setSubscriptionLoading(true);
      const subscriptionService = (
        await import("../services/subscriptionService")
      ).default;
      const data = await subscriptionService.getCurrentSubscription();
      if (data.success) {
        setSubscription(data.data);
        setHasActiveSubscription(data.hasActiveSubscription);
        setIsInGracePeriod(data.isInGracePeriod || false);
      }
    } catch (err) {
      console.error("Error loading subscription:", err);
    } finally {
      setSubscriptionLoading(false);
    }
  };

  const loadPlans = async () => {
    try {
      const response = await fetch("/api/subscriptions/plans");
      const data = await response.json();
      if (data.success) {
        setPlans(data.data);
      }
    } catch (err) {
      console.error("Error loading plans:", err);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    // Basic validation
    if (!formData.nombres || !formData.apellidos) {
      return setError("Nombres y apellidos son campos requeridos");
    }

    try {
      setError("");
      setLoading(true);

      const result = await updateProfile(formData);

      if (result.error) {
        throw new Error(result.error);
      }

      setSuccess(true);
      setIsEditing(false);

      // Clear success message after delay
      setTimeout(() => {
        setSuccess(false);
      }, 3000);
    } catch (error) {
      setError("Error al actualizar perfil: " + error.message);
    } finally {
      setLoading(false);
    }
  };

  const toggleEdit = () => {
    setIsEditing(!isEditing);
    setError("");
    setSuccess(false);
  };

  const handleSyncSubscription = async () => {
    if (!subscription?.subscriptionId) return;

    try {
      setSyncLoading(true);
      setError("");
      setSyncSuccess("");

      const subscriptionService = (
        await import("../services/subscriptionService")
      ).default;

      const result = await subscriptionService.syncSubscription(
        subscription.subscriptionId,
      );

      if (result.success) {
        setSyncSuccess("Suscripcin sincronizada correctamente");
        // Reload subscription data
        await loadSubscription();

        // Clear success message after 3 seconds
        setTimeout(() => setSyncSuccess(""), 3000);
      }
    } catch (err) {
      setError("Error al sincronizar suscripcin: " + err.message);
    } finally {
      setSyncLoading(false);
    }
  };

  const handleCancelSubscription = async () => {
    try {
      setActionLoading(true);
      setError("");
      setSuccess(false);

      const subscriptionService = (
        await import("../services/subscriptionService")
      ).default;
      const data = await subscriptionService.cancelSubscription(
        "Usuario solicit cancelacin",
      );

      if (data.success) {
        setSuccess(true);
        setShowCancelModal(false);
        await loadSubscription();
      } else {
        setError(data.error || "Error al cancelar la suscripcin");
      }
    } catch (err) {
      setError("Error al cancelar la suscripcin");
    } finally {
      setActionLoading(false);
    }
  };

  const getStatusBadge = (status) => {
    const statusConfig = {
      active: { variant: "success", text: "Activa" },
      pending: { variant: "warning", text: "Pendiente" },
      cancelled: { variant: "danger", text: "Cancelada" },
      suspended: { variant: "secondary", text: "Suspendida" },
      expired: { variant: "dark", text: "Expirada" },
    };

    const config = statusConfig[status] || {
      variant: "secondary",
      text: status,
    };
    return <Badge bg={config.variant}>{config.text}</Badge>;
  };

  const formatPrice = (price) => {
    return new Intl.NumberFormat("es-US", {
      style: "currency",
      currency: "USD",
    }).format(price);
  };

  const formatDate = (date) => {
    if (!date) return "N/A";
    return new Date(date).toLocaleDateString("es-ES");
  };

  if (profileLoading) {
    return (
      <Container className="py-5 text-center">
        <Spinner animation="border" role="status" variant="primary">
          <span className="visually-hidden">Cargando...</span>
        </Spinner>
        <p className="mt-3">Cargando informacin de perfil...</p>
      </Container>
    );
  }

  if (!currentUser) {
    return (
      <Container className="py-5">
        <Alert variant="warning">
          No has iniciado sesin. Por favor inicia sesin para ver tu perfil.
        </Alert>
        <div className="text-center mt-3">
          <Button onClick={() => navigate("/login")}>Iniciar Sesin</Button>
        </div>
      </Container>
    );
  }

  if (!userProfile) {
    return (
      <Container className="py-5">
        <Alert variant="warning">
          No se encontr informacin de perfil. Por favor complete su perfil
          primero.
        </Alert>
        <div className="text-center mt-3">
          <Button onClick={() => navigate("/complete-profile")}>
            Completar Perfil
          </Button>
        </div>
      </Container>
    );
  }

  return (
    <Container className="py-5">
      <Row className="justify-content-center">
        <Col md={8}>
          <Card className="shadow">
            <Card.Body className="p-4">
              <div className="d-flex justify-content-between align-items-center mb-4">
                <h2 className="mb-0">Mi Perfil</h2>
                <div>
                  <small className="text-muted me-3">
                    Usuario: {currentUser.email}
                  </small>
                  <Button
                    variant={isEditing ? "secondary" : "primary"}
                    onClick={toggleEdit}
                    disabled={loading}
                  >
                    {isEditing ? "Cancelar" : "Editar Perfil"}
                  </Button>
                </div>
              </div>

              {error && <Alert variant="danger">{error}</Alert>}
              {success && (
                <Alert variant="success">
                  Perfil actualizado exitosamente!
                </Alert>
              )}

              <Form onSubmit={handleSubmit}>
                <Row>
                  <Col md={6}>
                    <Form.Group className="mb-3" controlId="nombres">
                      <Form.Label>Nombres</Form.Label>
                      <Form.Control
                        type="text"
                        name="nombres"
                        value={formData.nombres}
                        onChange={handleChange}
                        disabled={!isEditing}
                        required
                      />
                    </Form.Group>
                  </Col>
                  <Col md={6}>
                    <Form.Group className="mb-3" controlId="apellidos">
                      <Form.Label>Apellidos</Form.Label>
                      <Form.Control
                        type="text"
                        name="apellidos"
                        value={formData.apellidos}
                        onChange={handleChange}
                        disabled={!isEditing}
                        required
                      />
                    </Form.Group>
                  </Col>
                </Row>

                <Form.Group className="mb-3" controlId="email">
                  <Form.Label>Correo Electrnico</Form.Label>
                  <Form.Control
                    type="email"
                    value={userProfile.email}
                    disabled
                    readOnly
                  />
                  <Form.Text className="text-muted">
                    Este campo no se puede modificar.
                  </Form.Text>
                </Form.Group>

                <Row>
                  <Col md={6}>
                    <Form.Group className="mb-3" controlId="telefono">
                      <Form.Label>Telfono</Form.Label>
                      <Form.Control
                        type="tel"
                        name="telefono"
                        value={formData.telefono}
                        onChange={handleChange}
                        disabled={!isEditing}
                      />
                    </Form.Group>
                  </Col>
                  <Col md={6}>
                    <Form.Group className="mb-3" controlId="pais">
                      <Form.Label>Pas</Form.Label>
                      <Form.Control
                        type="text"
                        name="pais"
                        value={formData.pais}
                        onChange={handleChange}
                        disabled={!isEditing}
                      />
                    </Form.Group>
                  </Col>
                </Row>

                {isEditing && (
                  <div className="d-grid gap-2 mt-4">
                    <Button variant="success" type="submit" disabled={loading}>
                      {loading ? "Guardando..." : "Guardar Cambios"}
                    </Button>
                  </div>
                )}
              </Form>

              <div className="mt-4">
                <Card.Text>
                  <strong>Fecha de registro:</strong>{" "}
                  {new Date(userProfile.createdAt).toLocaleDateString()}
                </Card.Text>
                {userProfile.updatedAt &&
                  userProfile.updatedAt !== userProfile.createdAt && (
                    <Card.Text>
                      <strong>ltima actualizacin:</strong>{" "}
                      {new Date(userProfile.updatedAt).toLocaleDateString()}
                    </Card.Text>
                  )}
              </div>
            </Card.Body>
          </Card>

          {/* Subscription Section */}
          <Card className="shadow mt-4">
            <Card.Body className="p-4">
              <div className="d-flex justify-content-between align-items-center mb-3">
                <h3 className="mb-0">Mi Suscripcin</h3>
                <Button
                  variant="outline-primary"
                  size="sm"
                  onClick={() => navigate("/subscriptions")}
                >
                  Ver Todos los Planes
                </Button>
              </div>

              {subscriptionLoading ? (
                <div className="text-center py-3">
                  <Spinner animation="border" size="sm" />
                  <p className="text-muted mt-2">
                    Cargando informacin de suscripcin...
                  </p>
                </div>
              ) : hasActiveSubscription && subscription ? (
                <div>
                  <Card className="border-success mb-3">
                    <Card.Header className="bg-success text-white">
                      <h5 className="mb-0">Plan Actual</h5>
                    </Card.Header>
                    <Card.Body>
                      <Row className="mb-3">
                        <Col md={6}>
                          <h6>
                            Plan:{" "}
                            <Badge bg="primary">
                              {plans[subscription.planType]?.name ||
                                subscription.planType.charAt(0).toUpperCase() +
                                  subscription.planType.slice(1)}
                            </Badge>
                          </h6>
                          <p>
                            <strong>Estado:</strong>{" "}
                            {getStatusBadge(subscription.status)}
                          </p>
                          <p>
                            <strong>Precio:</strong>{" "}
                            {formatPrice(subscription.price)}/mes
                          </p>
                        </Col>
                        <Col md={6}>
                          <p>
                            <strong>Fecha de inicio:</strong>{" "}
                            {formatDate(subscription.startDate)}
                          </p>
                          <p>
                            <strong>Prximo cobro:</strong>{" "}
                            {formatDate(subscription.nextBillingDate)}
                          </p>
                          {subscription.endDate && (
                            <p>
                              <strong>Fecha de fin:</strong>{" "}
                              {formatDate(subscription.endDate)}
                            </p>
                          )}
                          {isInGracePeriod && (
                            <Alert variant="warning" className="mt-2">
                              <i className="bi bi-exclamation-triangle me-2"></i>
                              <strong>Suscripcin cancelada:</strong> Mantienes
                              acceso hasta la fecha de fin.
                            </Alert>
                          )}
                        </Col>
                      </Row>

                      {/* Action Buttons */}
                      <Row className="mt-3">
                        <Col>
                          <div className="d-flex gap-2 flex-wrap">
                            {subscription.status === "pending" && (
                              <>
                                {autoSyncActive ? (
                                  <Alert variant="info" className="mb-2 w-100">
                                    <div className="d-flex align-items-center">
                                      <Spinner
                                        animation="border"
                                        size="sm"
                                        className="me-2"
                                      />
                                      <span>
                                        <strong>
                                          Sincronizando automticamente con
                                          PayPal...
                                        </strong>
                                      </span>
                                    </div>
                                    <small className="text-muted mt-1 d-block">
                                      Estamos verificando el estado de tu
                                      suscripcin automticamente.
                                    </small>
                                  </Alert>
                                ) : (
                                  <Button
                                    variant="outline-warning"
                                    onClick={handleSyncSubscription}
                                    disabled={syncLoading || actionLoading}
                                    size="sm"
                                  >
                                    {syncLoading ? (
                                      <>
                                        <Spinner
                                          animation="border"
                                          size="sm"
                                          className="me-1"
                                        />
                                        Sincronizando...
                                      </>
                                    ) : (
                                      <>
                                        <i className="bi bi-arrow-clockwise me-1"></i>
                                        Sincronizar con PayPal
                                      </>
                                    )}
                                  </Button>
                                )}
                              </>
                            )}
                            {!isInGracePeriod && (
                              <Button
                                variant="outline-danger"
                                onClick={() => setShowCancelModal(true)}
                                disabled={
                                  actionLoading || syncLoading || autoSyncActive
                                }
                                size="sm"
                              >
                                Cancelar Suscripcin
                              </Button>
                            )}
                          </div>
                        </Col>
                      </Row>

                      {syncSuccess && (
                        <Alert variant="success" className="mb-0 mt-2">
                          <i className="bi bi-check-circle me-2"></i>
                          {syncSuccess}
                        </Alert>
                      )}
                    </Card.Body>
                  </Card>
                </div>
              ) : (
                <div>
                  <Alert variant="info">
                    <h6>
                      <i className="bi bi-info-circle me-2"></i>Sin Suscripcin
                      Activa
                    </h6>
                    <p className="mb-3">
                      Actualmente no tienes una suscripcin activa. Para acceder
                      a todas las funciones premium de NexoSQL, elige uno de
                      nuestros planes.
                    </p>
                    <div className="d-grid gap-2 d-md-flex">
                      <Button
                        variant="primary"
                        onClick={() => navigate("/subscriptions")}
                      >
                        Ver Planes de Suscripcin
                      </Button>
                    </div>
                  </Alert>
                </div>
              )}
            </Card.Body>
          </Card>
        </Col>
      </Row>

      {/* Cancel Subscription Modal */}
      <Modal show={showCancelModal} onHide={() => setShowCancelModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Cancelar Suscripcin</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>Ests seguro de que quieres cancelar tu suscripcin?</p>
          <Alert variant="warning">
            <strong>Importante:</strong> Una vez cancelada, perders el acceso a
            las funciones premium al final del perodo de facturacin actual.
          </Alert>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowCancelModal(false)}>
            No, Mantener Suscripcin
          </Button>
          <Button
            variant="danger"
            onClick={handleCancelSubscription}
            disabled={actionLoading}
          >
            {actionLoading ? (
              <Spinner animation="border" size="sm" />
            ) : (
              "S, Cancelar"
            )}
          </Button>
        </Modal.Footer>
      </Modal>
    </Container>
  );
};

export default Profile;
