import React, { useState } from "react";
import {
  Container,
  Row,
  Col,
  Card,
  Form,
  Button,
  Alert,
} from "react-bootstrap";
import { Link, useNavigate, useLocation } from "react-router-dom";
import { useAuth } from "../contexts/AuthContext";
import logoNexoSQL from "../assets/logo_nexosql.svg";

const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);

  const { login, loginWithGoogle } = useAuth();
  const navigate = useNavigate();
  const location = useLocation();
  const from = location.state?.from || "/welcome";

  const handleEmailLogin = async (e) => {
    e.preventDefault();

    // Basic validation
    if (!email || !password) {
      return setError("Por favor ingrese email y contrasea");
    }

    try {
      setError("");
      setLoading(true);

      const result = await login(email, password);

      if (result.error) {
        throw new Error(result.error);
      }

      // Login successful, redirect to the page user was trying to access
      navigate(from);
    } catch (error) {
      setError("Error al iniciar sesin: " + error.message);
    } finally {
      setLoading(false);
    }
  };

  const handleGoogleLogin = async () => {
    try {
      setError("");
      setLoading(true);

      const result = await loginWithGoogle();

      if (result.error) {
        throw new Error(result.error);
      }

      // Login successful, redirect to the page user was trying to access
      navigate(from);
    } catch (error) {
      setError("Error al iniciar sesin con Google: " + error.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Container className="py-5">
      <Row className="justify-content-center">
        <Col md={6}>
          <Card className="shadow">
            <Card.Body className="p-4">
              <div className="text-center mb-4">
                <div className="d-flex align-items-center justify-content-center">
                  <img
                    src={logoNexoSQL}
                    alt="NexoSQL Logo"
                    style={{ height: "80px", marginRight: "15px" }}
                  />
                  <h1
                    className="mb-0"
                    style={{ color: "#007bff", fontWeight: "bold" }}
                  >
                    NexoSQL
                  </h1>
                </div>
              </div>
              <h2 className="text-center mb-4">Iniciar Sesin</h2>

              {error && <Alert variant="danger">{error}</Alert>}

              <Form onSubmit={handleEmailLogin}>
                <Form.Group className="mb-3" controlId="email">
                  <Form.Label>Correo Electrnico</Form.Label>
                  <Form.Control
                    type="email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    placeholder="Ingrese su correo electrnico"
                    required
                  />
                </Form.Group>

                <Form.Group className="mb-3" controlId="password">
                  <Form.Label>Contrasea</Form.Label>
                  <Form.Control
                    type="password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    placeholder="Ingrese su contrasea"
                    required
                  />
                </Form.Group>

                <div className="d-grid gap-2">
                  <Button variant="primary" type="submit" disabled={loading}>
                    {loading ? "Cargando..." : "Iniciar Sesin"}
                  </Button>

                  <div className="text-center my-3">O</div>

                  <Button
                    variant="outline-danger"
                    onClick={handleGoogleLogin}
                    disabled={loading}
                    type="button"
                  >
                    Iniciar Sesin con Google
                  </Button>
                </div>
              </Form>

              <div className="text-center mt-3">
                No tiene una cuenta? <Link to="/register">Regstrese</Link>
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default Login;
